import { Apis as CybexWs } from "cybexjs-ws";

class Api {
  apiInstance = null;

  async init() {
    const nodeUrl = 'wss://hangzhou.51nebula.com';
    this.apiInstance = CybexWs.instance(nodeUrl, true);
    return await this.apiInstance.init_promise;
  }

  instance() {
    return this.apiInstance;
  }
}

export const Apis = new Api();



